import React from "react"

import Layout from "../components/layout/layout"
// import HeroSection from "../components/heroSection/HeroSection"
// import ContentSection from "../components/contentSection/contentSection"
// import ClientLogo from "../components/clientsLogo/clientsLogo"
import { HomeWrapper, BlogWrapper } from "../components/home/home.css"
import { useStaticQuery, graphql } from "gatsby"
import BlogCard from "../components/blogCard/blogCard"
import { SearchContainer } from "../components/nav/nav.css"
import Category from "../components/category/category";

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query ServicesQuery {
      allStrapiArticle {
        edges {
          node {
            strapiId
            id
            Title
            Description
            category {
              name
            }
            Image {
              childImageSharp {
                fluid(
                  maxHeight: 300,
                  quality: 90,
                  traceSVG: {
                    color: "#292733"
                    turnPolicy: TURNPOLICY_MAJORITY
                  }
                ) {
                  ...GatsbyImageSharpFluid_withWebp_tracedSVG
                }
              }
            }
            user {
              username
              created_at(formatString: "DD MMM, YYYY")
              updated_at(formatString: "DD MMM, YYYY")
            }
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
      {/* home page starts */}
      <HomeWrapper>
        <BlogWrapper>
          <div className="container lg-container xs-fullwidth">
            <div className="row">
              <div className="col-sm-9">
              {
                data.allStrapiArticle.edges.map(item => (
                  <BlogCard
                    key={item.node.id}
                    {...item.node}
                  />
                ))
              }
              </div>
              <div className="col-sm-3">
                <SearchContainer>
                  <input type="text" placeholder="Search" />
                </SearchContainer>
                <Category />
              </div>
              {/* <div className="col-sm-6"><BlogCard /></div>
              <div className="col-sm-6"><BlogCard /></div>
              <div className="col-sm-6"><BlogCard /></div> */}
            </div>
          </div>
        </BlogWrapper>
        {/* {data.servicesJson.services.map((item, i) => {
            return (
              <ContentSection 
                key={i}
                id={i}
                rightSide={i%2}
                listWithIcons
                image1={item.image1.childImageSharp.fluid}
                image2={item.image2.childImageSharp.fluid}
                heading={item.title}
                description={item.content.childMarkdownRemark.rawMarkdownBody}
                category={item.category}
              />
            )
          })} */}
        {/* <ClientLogo /> */}
      </HomeWrapper>
      {/* home page ends */}
    </Layout>
  )
}

export default IndexPage
